import React, {useState} from "react";
import 'react-toastify/dist/ReactToastify.css';
import './Registration.css'
import { useRegistrationMutation} from "../../redux/api/authApi";
import {ToastWrapper, ToastWrapperType} from "../../Common/Components/ToastWrapper/ToastWrapper";
import { useNavigate } from "react-router-dom";
import {RoleTypes} from "../../router/AppRoute";


export const Registration = () => {
    const [registration, {isLoading, error}] = useRegistrationMutation()
    const navigate = useNavigate();

    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [password2, setPassword2] = useState('')
    const [role, setRole] = useState<RoleTypes | null>(null)
    const [errorText, setErrorText] = useState('')
    const [success, setSuccess] = useState('')

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (email === '') {
            setErrorText(' "email"  !!')
        } else if (password.length < 4 && password2.length < 4) {
            setErrorText('        4 !!')
        } else if (password !== password2) {
            setErrorText(' !!')
        } else if (role === null) {
            setErrorText('  ')
        } else {
            try {
                const data = await registration({email, password, role})
                if(!!data && !error){
                    ToastWrapper({
                        msg: "      ".replace(/"/g, ''),
                        type: ToastWrapperType.success,
                    })
                    navigate('/login')
                    // return () => <AppRedirect path="/" />
                }
            } catch (e) {
                console.log('error', e)
            }
        }
    };

    return (
        <div className="auth_wrap">
            {isLoading && <h1> </h1>}
            <div className="registration-block">
                <div className="header-block"><span></span></div>
                <form onSubmit={handleSubmit} className="form-block">
                    <div className="form-item">
                        <label htmlFor="email"> email:</label>

                        <input type="text" value={email} placeholder="  email" onChange={(e) => {
                            setErrorText('')
                            setSuccess('')
                            setEmail(e.currentTarget.value)
                        }} name="email"/>
                    </div>
                    <div className="form-item">
                        <label htmlFor="password"> :</label>
                        <input type="password" value={password} placeholder=" " onChange={(e) => {
                            setErrorText('')
                            setSuccess('')
                            setPassword(e.currentTarget.value)
                        }} name="password"/>
                    </div>
                    <div className="form-item">
                        <label htmlFor="password2">   :</label>
                        <input type="password" name="password2" value={password2} placeholder="   "
                               onChange={(e) => {
                                   setErrorText('')
                                   setSuccess('')
                                   setPassword2(e.currentTarget.value)
                               }}/>
                    </div>
                    <div className="form-item">
                        <label htmlFor="password2"> :</label>
                        <span>
                            <input
                                type="radio"
                                name="role"
                                value='USER'
                                onChange={(e)=> setRole(e.target.value as RoleTypes)}
                            />USER</span>
                        <span>
                            <input
                                type="radio"
                                name="role"
                                value="ADMIN"
                                onChange={(e)=> setRole(e.target.value as RoleTypes)}
                            />ADMIN
                        </span>
                    </div>
                    <div className="form-item btn-block">
                        <input type="submit" className="btn" value=""/>
                        {errorText && <div className="error-text">{errorText}</div>}
                        {success && <div className="success-text">{success}</div>}
                    </div>
                </form>
            </div>
        </div>
    )
}